h1 {
  font-size: 4rem;
  text-transform: capitalize;
  color: #f2cc8f;
}

h2 {
  color: #81b29a;
  font-size: 2rem;
  text-transform: capitalize;
}

.home {
  height: 100vh;
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  align-items: center;
  /* justify-content: space-evenly;
  flex-direction: row; */
  background-color: #3d405b;
  /* flex-wrap: wrap-reverse; */
}

.describe {
  /* max-width: 50%; */
  text-align: center;
  color: #f4f1de;
}

.describe .sosmed {
  /* text-align: center; */
  /* max-width: 75%; */
  margin-top: 1em;
}

.describe p {
  /* max-width: 75%; */
  font-size: 1.5rem;
  text-align: center;
  margin-top: 1em;
}

.fotoProfile {
  display: flex;
  text-align: center;
}

.fotoProfile img {
  width: 300px;
  border-radius: 1em;
}

.skill {
  text-align: left;
  background-color: #f4f1de;
  padding: 3em 0;
}

.skill h1 {
  text-align: center;
  text-transform: uppercase;
  color: #3d405b;
  margin: 0 0 1em 0;
}

.skills {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-items: center;
  /* flex-direction: row;
  justify-content: space-evenly;
  flex-wrap: wrap; */
}

.card-skill {
  border-radius: 1em;
  padding: 2em 3.5em;
  line-height: 2em;
  background-color: #f4f1de;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  width: 300px;
  margin: 2em;
}

.card-skill ul {
  margin-top: 1.5em;
}

.card-skill ul li {
  list-style: none;
  color: #3d405b;
  font-size: 1.25rem;
}

@media only screen and (max-width: 480px) {
  .skills {
    grid-template-columns: 1fr;
  }

  h1 {
    font-size: 2rem;
  }

  h2 {
    font-size: 1.5rem;
  }

  .fotoProfile img {
    width: 150px;
  }

  .describe p {
    font-size: 1rem;
  }
}

@media only screen and (min-width: 481px) and (max-width: 1024px) {
  .skills {
    grid-template-columns: 1fr 1fr;
  }
}

@media only screen and (max-width: 768px) {
  .home {
    grid-template-columns: 1fr;
    height: unset;
    padding: 5em 0;
  }

  .fotoProfile {
    grid-row: 1;
  }

  .describe {
    margin-top: 3em;
  }
}

