.project {
  background-color: #f4f1de;
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.project h1 {
  margin-top: 0.5em;
  font-size: 3rem;
  text-align: center;
  color: #3d405b;
}

.project h2 {
  font-size: 2rem;
}

.projectItem {
  border-radius: 15px;
  width: 300px;
  height: 300px;
  margin: 40px;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
  text-align: center;
}

.projectItem .bgImage {
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  width: 100%;
  height: 200px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}

.projectItem:hover {
  box-shadow: 0px 3px 15px #f2cc8f;
  transition: 0.3s ease-in;
  cursor: pointer;
}

.projectList {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  place-items: center;
}

/* css details project */
.details {
  background-color: #f4f1de;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.details img {
  width: 700px;
  border-radius: 10px;
  margin: 2em 0;
}

.details h1 {
  color: #81b29a;
  font-size: 3rem;
  margin-top: 0.5em;
}

.details a {
  color: red;
}

.details p {
  font-size: 2rem;
}

.details span {
  font-weight: 900;
  font-size: 2.2rem;
}

.details svg {
  font-size: 4rem;
  margin: 0.2em 0;
  color: #3d405b;
}

.details svg:hover {
  color: #81b29a;
  transition: 0.3s ease-in;
  cursor: pointer;
}

@media only screen and (max-width: 480px) {
  .projectList {
    grid-template-columns: 1fr;
  }

  .project h1 {
    font-size: 2rem;
  }

  .project h2 {
    font-size: 1.8rem;
  }

  /* details project */
  .details h1 {
    font-size: 2rem;
  }

  .details img {
    width: 360px;
  }

  .details svg {
    font-size: 2rem;
  }

  .details p {
    font-size: 1rem;
  }

  .details span {
    font-size: 1.6rem;
  }
}

@media only screen and (max-width: 1024px) and (min-width: 481px) {
  .projectList {
    grid-template-columns: 1fr 1fr;
  }

  .project h1 {
    font-size: 2.8rem;
  }

  .project h2 {
    font-size: 2rem;
  }

  /* details project */
  .details h1 {
    font-size: 2.8rem;
  }

  .details img {
    width: 600px;
  }

  .details svg {
    font-size: 3rem;
  }

  .details p {
    font-size: 1.6rem;
  }

  .details span {
    font-size: 2rem;
  }
}
